.bulbIcon {
  background-image: url(/img/bulb.png);
  width: 50px;
  height: 80px;
  background-size: contain;
  background-repeat: no-repeat;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.bulbIcon:hover {
  background-image: url(/img/bulb_hover.png);
}

.sensorIcon {
  background-image: url(/img/sensor.png);
  width: 50px;
  height: 80px;
  background-size: contain;
  background-repeat: no-repeat;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.sensorIcon:hover {
  background-image: url(/img/sensor_hover.png);
}

.switchIcon {
  background-image: url(/img/switch.png);
  width: 50px;
  height: 80px;
  background-size: contain;
  background-repeat: no-repeat;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.switchIcon:hover{
  background-image: url(/img/switch_hover.png);
}

html, body, .h100 {
  min-height: 100% !important;
  height: 100%;
}

.w100 {
  width: 100%;
}



